* {
    box-sizing: border-box;
  }

.faq_i {margin-top: 12vw;}

.fq_tm span {display: block; padding-top: 3%; margin-left: 5%; color: gray;}

.faq_wrap h2 {font-weight: inherit; font-size: 1.3rem; display: block; margin-left: 6%; margin-top: 3%;
    padding-bottom: 3%; font-weight: bold; color: rgba(0, 0, 0, 0.7);
    }

.fq_wh {width: 94%; margin-left: 3%; padding-top: 1%; padding-bottom: 1%; background: #f1f1ed;
        border-bottom: 2px solid rgba(0, 0, 0, 0.3); border-top: 2px solid rgba(0, 0, 0, 0.3);}
.fq_wh span, .fq_wh p {display: inline-block;}
.fq_wh span {width: 45%; margin-left: 30%;}
.fq_wh p {width: 25%; text-align: center;}

.fq_list {width: 94%; margin-left: 3%;}
.fq_list > li span, .fq_list > li p {display: inline-block; font-size: 1rem;}
.fq_list > li span {width: 73%; margin-left: 2%;}
.fq_list > li p {width: 25%; text-align: center;}
.fq_list > li {padding-top: 3%; padding-bottom: 3%; border-bottom: 1px solid rgba(0, 0, 0, 0.2);}

@media screen and (max-width: 400px) {
    .fq_wh span, .fq_wh p {font-size: 0.9rem;}
    .fq_list > li {padding-top: 2.5%; padding-bottom: 2.5%; border-bottom: 1px solid rgba(0, 0, 0, 0.2);}
    .fq_list > li span, .fq_list > li p {font-size: 0.9rem;}
}