* {
    box-sizing: border-box;
  }

.b_cover {position: fixed; top: 0; left: 0; right: 0; bottom: 0; background: rgba(0, 0, 0, 0.8);
    opacity: 0; visibility: hidden; z-index: 998; width: 100%; height: 100vh;}
.back_on {opacity: 1; visibility: visible;}
.body_lock {height: 100%; overflow: hidden;}

/* 캘린더 */
.cal_w {width: 90%; height: 86vh; position: fixed; z-index: 1600; background: #fff; top: 7vh; left: 5%;
        border-radius: 10px; border: 2px solid gray; display: none;}
.cal_w h3 {font-weight: inherit; font-size: 1.2rem; display: block; margin-top: 5%; text-align: center;}
.cal_w span {display: block; margin-top: 5%; text-align: center;}
.cancel, .mys {width: 80%; border-radius: 5px; border: 1px solid rgba(0, 0, 0, 0.2); cursor: pointer;
                height: 5vh; font-size: 1rem; display: block; margin-left: 10%; background: black;
                color: white;}
.cancel {position: absolute; top: 85%;}
.mys {position: absolute; top: 92%;}


/* Header 컴포넌트 */

.mt_head {width: 100%;height: 12vw; background: #ffffff; position: fixed; top: 0;
    border-top: 1px solid rgba(128, 128, 128, 0.4);
    border-bottom: 1px solid rgba(128, 128, 128, 0.4); z-index: 100;}
.mt_head > ul > li:first-child img {width: 22%; position: fixed; top: 1.3%; left: 39%;}
.lg_i {position: fixed; top: 2%; left: 73%; font-size: 1.1rem;}
.mt_head > ul > li:last-child img {width: 9%; position: fixed; top: 1.5%; left: 87%;}

/* Mmenu(햄버거) 컴포넌트 */

.hm_menu {position: fixed; right: -100%; top: 0; width: 100%; height: 100vh; background: #efeeea; 
    overflow-y: scroll; border-left: 1px solid black; z-index: 999;}

.h_tm {width: 100%; height: 8vh; position: relative; border-bottom: 3px solid rgba(0 , 0, 0, 0.3);
background: #fff;}
.h_tm span {position: absolute; top: 35%; font-size: 1.1rem; left: 7%;}
.h_tm > a:nth-child(2) {position: absolute; top: 35%; right: 15%; font-size: 1.1rem;}
.lo_btn > img, .hx_btn > img {width: 8%; position: absolute;}
.lo_btn > img {right: 14%; top: 12%;}
.hx_btn > img {right: 3%; top: 22%;}

.h_mtm {width: 100%; margin-top: 8%; border-bottom: 1px solid rgba(0 , 0, 0, 0.3);}
.h_mtm img {width: 30%; margin-right: 3%; border-radius: 10px; margin-bottom: 8%;}
.h_mtm_f {margin-left: 1.8%;}
.h_lm img {margin-right: 0;}


.h_sm > ul > li {padding-top: 4%; padding-bottom: 4%; border-bottom: 1px solid rgba(0 , 0, 0, 0.3); text-align: center;}
.h_sm > ul > li > div {position: relative;}
.h_sm > ul > li > div > a > span {font-size: 1.05rem;}
.h_sm img {width: 6%; position: absolute; top: -5%; right: 3%; transition: 0.5s;}
.h_sm img.open {transform: rotate(135deg);}
.h_sml > a > span {font-size: 1.05rem;}
.sm_s {text-align: right; margin-right: 5%; display: none;}
.sm_s > li {padding-top: 3%;}
.sm_s > li:first-child {padding-top: 6%;}
.sm_s > li:last-child {padding-bottom: 2%;}

/* Mban */

nav {margin-top: 12vw;}
.Mainb {width: 100%;}
.Mainb > img {width: 100%;}

.mb_b {margin-top: -15%; position: relative; width: 96%; margin-left: 3.5%;}
.mb_b > li {width: 30%; max-width: 140px;
       background: #fff;
       height: 120px;
       border: 1px solid rgba(0, 0, 0, 0.2);
       border-radius: 10px;
       box-shadow: 2px 2px 1px 1px rgba(0, 0, 0, 0.5);
       padding-top: 1.5%; display: inline-block;
       vertical-align: middle; margin-right: 3%;}
.mb_b > li > a > img {width: 15vw; display: block; margin: 0 auto;}
.mb_b > li > a > span {display: block; text-align: center; font-size: 0.9rem; margin-top: 3%;}
.mb_b > li:nth-child(2) > a > span, .mb_b > li:nth-child(4) > a > span {margin-top: 10%;}
.mb_b > li:nth-child(3) {margin-right: 0;}
.mb_b > li:nth-child(4), .mb_b > li:last-child {margin-top: 3%;}
.mb_b > li:nth-child(4) {margin-left: 17%;}

/* Banb */

   .Banb {margin-top: 5%; width: 100%;}
   .Banb > ul {width: 96%; margin-left: 2%; display: flex;}
   .Banb > ul > li {display: inline; width: 49%; border: 1px solid rgba(0, 0, 0, 0.4); border-radius: 5px;}
   .Banb > ul > li:last-child {margin-left: 2%;}
   .Banb > ul > li > a > img {width: 94%; margin: auto; display: block; margin-top: 3%;
       border: 1px solid rgba(0, 0, 0, 0.3); border-radius: 5px;}
   .Banb > ul > li > a > span {display: block; font-size: 0.9rem; margin-top: 3%; text-align: center;
       padding-bottom: 5%;}
   .Banb > ul > li:last-child > a > span {margin-top: 8%;}

    /* Banb_Slider */

    .slick-slide img {margin-left: 5px;}
    .s_wrap {border-top: 1px solid rgba(0, 0, 0, 0.2); border-bottom: 1px solid rgba(0, 0, 0, 0.2);
        margin-top: 5%;}
    .slider-container {width: 97%; margin-left: 3%; margin-top: 5.5%; margin-bottom: 5%;}
    .slider-container img {border-radius: 10px; border: 1px solid rgba(0, 0, 0, 0.2);
    box-shadow: 1px 1px 5px; width: 90%;}

   
/* Subb */

.Sub_n {margin-top: 3%;}
.notice {border-top: 2px solid rgba(0, 0, 0, 0.3);
         border-bottom: 2px solid rgba(0, 0, 0, 0.3);
         width: 94%; margin-left: 3%;}
.notice > div {position: relative;}
.notice > div > span {display: block; text-align: center; padding-top: 1.2%; width: 100%; background: #efeeea;
                border-bottom: 2px solid rgba(0, 0, 0, 0.3); padding-bottom: 1.2%;}
.notice > div > a > img {position: absolute; width: 5%; top: 15%; right: 3%;}
.notice > ul > li span, .notice > ul > li p {display: inline-block; font-size: 1rem;}
.notice > ul > li span {width: 65%; margin-left: 3%;}
.notice > ul > li p {width: 24%; margin-left: 7%; margin-right: 1%; text-align: right;}
.notice > ul > li {padding-top: 2%; padding-bottom: 2%; border-bottom: 1px solid rgba(0, 0, 0, 0.2);}

.faq {border-top: 2px solid rgba(0, 0, 0, 0.3);
    border-bottom: 2px solid rgba(0, 0, 0, 0.3);
    width: 94%; margin-left: 3%; margin-top: 5%;}
.faq > div {position: relative;}
.faq > div > span {display: block; text-align: center; padding-top: 1.2%; width: 100%; background: #efeeea;
           border-bottom: 2px solid rgba(0, 0, 0, 0.3); padding-bottom: 1.2%;}
.faq > div > a > img {position: absolute; width: 5%; top: 15%; right: 3%;}
.faq > ul > li span, .faq > ul > li p {display: inline-block; font-size: 1rem;}
.faq > ul > li span {width: 65%; margin-left: 3%;}
.faq > ul > li p {width: 24%; margin-left: 7%; text-align: right; margin-right: 1%;}
.faq > ul > li {padding-top: 2%; padding-bottom: 2%; border-bottom: 1px solid rgba(0, 0, 0, 0.2);}

/* Foot 컴포넌트 */

footer {border-top: 2px solid rgba(0, 0, 0, 0.2); margin-top: 3%; width: 90%; margin-left: 5%;}
.ff_i {text-align: center;}
.ff_i > a > span {display: inline-block;}
.ff_i > a:first-child > span {margin-top: 5%;}
.ff_i > a:last-child > span {margin-top: 3%;}
.f_end {text-align: center; margin-top: 5%;}
.f_end > a:first-child img {width: 18%;}
.mili {width: 30%;}
.f_end {padding-bottom: 3%;}
.f_end > span {display: block; margin-top: 5%; font-size: 0.9rem; color: gray; opacity: 0.8;}

@media screen and (max-width: 400px) {
    .h_tm {width: 100%; height: 8vh; position: relative; border-bottom: 3px solid rgba(0 , 0, 0, 0.3);
        background: #fff;}
    .h_tm span {top: 30%; font-size: 1rem; left: 7%;}
    .h_tm > a:nth-child(2) {top: 30%; right: 15%; font-size: 1rem;}
    .lg_i {top: 1.6%; left: 73%; font-size: 1rem;}
    .notice > ul > li span, .notice > ul > li p {display: inline-block; font-size: 0.9rem;}
    .faq > ul > li span, .faq > ul > li p {font-size: 0.9rem;}
    .mb_b > li {height: 110px;}
}