@charset "utf-8";

*,*::after, *::before {box-sizing: border-box;}
a {text-decoration: none; color: inherit;}
@font-face {
    font-family: "Roboto";
    font-style: normal;
    src: url("../font/Roboto-Medium.woff") format("woff");
    unicode-range: U+0041-005A, U+0061-007A;
}
@font-face {
    font-family: "NotoSansKR";
    font-style: normal;
    src: url("../font/NotoSansKR-Medium.woff") format("woff");
    unicode-range: U+1100-11FF, U+3130-318F, U+A960-A97F, U+AC00-D7A3, U+D7B0-D7FF, U+0030-0039;
}
body {font-family: "NotoSansKR", "Roboto";}
.b_cover {position: fixed; top: 0; right: -100%; bottom: 0; background: rgba(0, 0, 0, 0.8);
          width: 15%;}
.body_lock {height: 100%; overflow: hidden;}